.one_category{
    padding: 8px 8px 0px;
    margin: 5px;
    width: 200px;
    border-radius: 10px;
    background-color: white;
    transition: scale 350ms;
}
.one_category:hover{
    scale: 1.02;
}
@media (min-width:768px) {
    .one_category{
        width: 300px;
    }
}
@media (max-width:500px) {
    .one_category{
        width: 100px;
    }
}
.one_category p{
    font-size: 14px;
    margin: 0;
}
.one_category span {
    font-size: 12px;
    color: #777;
}
.one_category p ,.one_category span{
    padding: 6px;
}
@media (min-width:768px) {
    .one_category p{
        font-size: 18px;
    }
    .one_category span {
        font-size: 14px;
    }
}
@media (max-width:500px) {
    .one_category p{
        font-size: 14px;
    }
    .one_category span {
        font-size: 10px;
    }
    .one_category p ,.one_category span{
        padding: 2px;
    }
}
.one_category img{
    max-width: 100%;
}