.av{
    position: relative;
    margin-bottom: 14px;
}
.av span {
    position: absolute;
    display: block;;
    width: 14px;
    height: 14px;
    background-color: var(--sc);
    border-radius: 50%;
    left: calc(50% - 10px);
    bottom: -14px;
    visibility: hidden;
}
.av span.active{
    visibility: visible;
}