@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Signika+Negative:wght@600&display=swap');
.taskForm{
    width: 100vw;
    height: 100vh;
    background-color: #0000006e;    
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999999;
    color: var(--mc);
    font-family: 'Signika Negative', sans-serif;
}
.content{
    display: flex;
    flex-direction: column;
    border: 4px solid var(--sc);
    padding: 14px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 60%;
    background-color: var(--tc);
    border-radius: 16px;
    transition: 350ms;
}
@media (max-width:992px) {
    .content{
        width: 70%;
    }
}
@media (max-width:768px) {
    .content{
        width: 90%;
    }
}
.content h2 {
    text-align: center;
}
.content > div {
    margin: 5px 0;
    display: flex;
    align-items: center;
}
.content input {
    font-family: 'Lobster', cursive; 
    max-width: 100%;
    flex-grow: 1;
    border: 0;
    outline: none;
    padding: 8px 12px;
    background-color: var(--sc);
    font-size: 18px;
}
.content input::placeholder {
    color: var(--mc);
    opacity: 0.7;
}

.radio{
    display: flex;
}
.radio > div {
    display: flex;
    justify-content: center;
    margin: 0 6px 0 0;
}
.radio input {
    width: auto;
}
.radio input[type="radio"]{
    margin: 0 6px 0 0 ;
    padding: 0;
    width: 16px;
    height: 16px;
    appearance: none;
    border-radius: 50%;
    opacity: 0.5;
    cursor: pointer;
}
.radio input[type="radio"]:checked{
    opacity: 1;
}


.exitPanel{
    position: absolute;
    top: -26px;
    right: 20px;
    width: 20px;
    height: 20px;
    background-color: var(--sc);
    color: var(--tc);
    cursor: pointer;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.exitPanel p{
    width: 100%;
    text-align: center;
}


.add{
    justify-content: center;
}
.add button {
    font-family: 'Signika Negative', sans-serif;
    border: 0;
    outline: none;
    background-color: var(--sc);
    font-weight: bold;
    color: var(--tc);
    padding: 8px 22px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 6px;
    transition: scale 350ms,color 350ms;
}
.add button:hover{
    scale: 1.05;
    color: var(--mc);
}


.info > div:first-child {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: var(--sc);
    color: var(--tc);
}
.info span{
    font-size: 30px;
}
.message{
    padding: 8px;
    background-color: #adadad ;
    height: 40px;
    display: flex;
    justify-content: left;
    align-items: center;
    min-width: 150px;
}
.message p {
    margin: 0;
    font-size: 14px;
}