@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Signika+Negative:wght@600&display=swap');
.home{
    min-height: calc( 100vh - (60px + 24px));
    padding: 12px 68px;
    font-family: 'Signika Negative', sans-serif;
}
@media (max-width:760px) {
    .home{
        padding: 12px 40px;
    }
}
.home > h1{
    text-align: center;
    font-size: 45px;
}
@media (max-width:760px) {
    .home > h1{
        text-align: start;
        font-size: 30px;
    }
}
.home > h1 span{
    color: var(--sc);
}
.home img{
    max-width: 100%;
    width: 70%;
    border-radius: 12px;
    object-fit: cover;
    object-position: center;
}
@media (max-width:991px) {
    .home img{
        width: 100%;
        border-radius: 8px;
    }
}

.description{
    display: flex;
    flex-direction: row;
}
.description p {
    margin: 6px 8px;
    font-size: 22px;
}
@media (max-width:991px) {
    .description{
        flex-direction: column;
    }
    .description p {
        margin: 8px 0px;
        font-size: 18px;
    }
}
.btn{
    text-align: start;
    padding: 24px 0px;
    margin: 0px 8px;
}
@media (max-width:991px) {
    .btn{
        text-align: center;
    }
}
.btn button{
    padding: 10px 30px ;
    border: 0;
    outline: none;
    border-radius: 50px;
    background-color: var(--sc);
    font-weight: 500;
    font-size: 18px;
    color: var(--tc);
    font-family: 'Lobster', cursive;
    transition: color 350ms;
}
.btn button:hover{
    color: var(--mc);
}