@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Signika+Negative:wght@600&display=swap');
.content{
    position: relative;
    padding: 12px 64px;
    min-height: calc( 100vh - (60px + 24px));
    font-family: 'Signika Negative', sans-serif;
}
@media (max-width:768px) {
    .content {
        padding: 12px 32px;
    }
}
.categories {
    display: flex;
    flex-wrap: wrap;
}


.tasks > div {
    display: flex;
    flex-wrap: wrap;
}

.empty{
    width: 100%;
    display: flex;
    justify-content: center;
}
.empty img {
    width: 60%;
    opacity: 0.6;
}
@media (min-width:768px) {
    .empty img {
        max-width: 30%;
    }
}