@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Signika+Negative:wght@600&display=swap');
.settings{
    color: var(--mc);
    padding: 12px 68px;
    font-family: 'Signika Negative', sans-serif;
}
@media (max-width:768px){
    .settings{
        padding: 12px 40px;
    }
}
.settings span:first-child {
    display: block;
    font-size: 20px;
    font-weight: 700;
}

.inputs{
    padding: 20px 0px;
}
.inputs input{
    font-family: 'Lobster', cursive;
    margin: 0px 8px;
    padding: 12px 14px;
    border: 0;
    outline: none;
    background-color: var(--mc);
    color: var(--tc);
    border-radius: 40px;
    width: 50%;
}
@media (max-width:568px) {
    .inputs input{
        width: auto;
    }
}
.inputs label{
    padding: 6px;
    color: var(--sc);
}
.inputs > div > div{
    padding: 6px 0px;
}
.avatars{
    padding: 15px 0px;
}
.avatars > div{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.inputs img{
    margin-right: 6px;
}

.inputs button{
    border: 0;
    outline: none;
    margin-top: 10px;
    padding: 8px 14px;
    background-color: var(--sc);
    color: var(--mc);
    cursor: pointer;
    font-weight: 600;
    border-radius: 4px;
    transition: color 350ms;
}

.inputs button:hover{
    color: var(--tc);
}