@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Rubik:ital,wght@1,600&display=swap');
.header{
    background-color: var(--sc);
    padding: 12px 24px;
    font-family: 'Lobster', cursive;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header >div{
    display: flex;
    align-items: center;
}
.header img{
    margin: 0 2px;
}
.header p {
    margin: 0 8px;
    font-size: 20px;
}
.header a{
    color: var(--tc);
}