.task {
    margin: 10px;
    padding: 12px;
    background-color: white;
    border-radius: 10px;
    position: relative;
    transition: scale 350ms,opacity 350ms;
}
.task a {
    text-decoration: none;
    color: var(--mc);
}
.task.end{
    opacity: 0.5;
}
.task:hover{
    scale: 1.02;
}
@media (max-width:991px) {
    .task {
        width: 40%;
    }   
}
@media (max-width:468px) {
    .task {
        width: 100%;
    }   
}
.task:hover{
    opacity: 1;
}
.task p {
    margin: 0;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
}
.task span{
    font-size: 10px;
    display: block;
    padding: 4px 0px;
    color: #777;
}

span.counter{
    background-color: var(--sc);
    font-size: 9px;
    width: 22px;
    height: 15px;
    border-radius: 10px;
    color: var(--tc);
    display: flex;
    justify-content: center;
    align-items: center;
}

.clock{
    display: flex;
    align-items: center;
}
.clock i{
    font-size: 18px;
    color: var(--sc);
}
.clock span{
    margin: 0 6px;
    font-size: 13px;
    color: var(--mc);
}
.complete{
    position: absolute;
    bottom: 3px;
    left: 0;
    height: 3px;
    background-color: var(--sc);
}


.dead_line{
    margin: 0 6px;
    padding: 2px 4px;
    font-size: 12px !important;
background: linear-gradient(to right,#006dff,#006effa3);
    color: var(--tc) !important;
    border-radius: 6px;

}


.delete{
    border: 0;
    outline: none;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    top: 40px;
    right: -10px;
    background:linear-gradient(to left,#dc80807d,var(--sc));
    border-radius: 50%;
    opacity: 0;
    cursor: pointer;
    transition: 350ms;
}
.delete span{
    font-size: 20px;
    color: var(--tc);
}
.task:hover .delete{
    right: 10px;
    opacity: 1;
}