:root{
  --mc:#202124;
  --sc:#FF7461;
  --tc:#F6FAFB;
}
body{
  background-color: var(--tc);
  margin: 0;
  padding: 0;
  color: var(--tc);
  max-width: 2400px;
  margin: auto;
}
*{
  box-sizing: border-box;
}
::selection{
  background-color: var(--sc);
  color: var(--tc);
}
#addTaskStyle{
  font-size: 25px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999999;
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 36px;
  height: 36px;
  background-color: var(--sc);
  border-radius: 50%;
  transition: scale 350ms;
}
#addTaskStyle:hover{
  scale: 1.1;
}